import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppSettings } from 'src/app/core/app-settings';
@Injectable({
  providedIn: 'root'
})
export class VendorService {
  baseUrl = AppSettings.API_ENDPOINT;
  constructor(private http: HttpClient) { }
  // Method: declaring api to fetch items for category-listing
  // tslint:disable-next-line: typedef
  getVendorList(queryStr: string) {
    if (!!queryStr) {
        queryStr = '?' + queryStr;
    } else {
        queryStr = '';
    }
    return this.http.get<any>(this.baseUrl + 'admin/api/vendor/getAllVendors/' + queryStr);
  }
   // Method: getting Type from BE
   gettypeDropdown(queryStr: string) {
    if (!!queryStr) {
        queryStr = '?' + queryStr;
    } else {
        queryStr = '';
    }
    return this.http.get<any>(this.baseUrl + 'api/user/eproducts/get/getEnumByName' + queryStr);
  }
  // Method: Save Vendor Data from BE
  saveVendorData(data: any) {
    return this.http.post<any>(this.baseUrl + 'admin/api/vendor/addNewVendor/', data);
  }
  // Method: Edit Vendor Data from BE
  editVendorData(data: any) {
    return this.http.post<any>(this.baseUrl + 'admin/api/vendor/updateVendor/', data);
  }
   // Method: declaring api to view item details
  getVendorDetailById(queryStr: string) {
    if (!!queryStr) {
        queryStr = '?' + queryStr;
    } else {
        queryStr = '';
    }
    return this.http.get<any>(this.baseUrl + 'admin/api/vendor/getVendorDetails' + queryStr);
  }
  // Method: declaring api to view item details
  getpincodedetails(queryStr: string) {
    if (!!queryStr) {
        queryStr = '?' + queryStr;
    } else {
        queryStr = '';
    }
    return this.http.get<any>(this.baseUrl + 'public/api/get/locationByPincode' + queryStr);
  }
   // Method: declaring api to delete items from category-listing
  removeVendor(queryStr: string) {
    if (!!queryStr) {
      queryStr = '?' + queryStr;
    } else {
        queryStr = '';
    }
    return this.http.delete<any>(this.baseUrl + 'admin/api/vendor/removeVendor' + queryStr);
  }
  // End of above code
   // Method: declaring api to fetch items for category-listing
  getCategoryList(queryStr: string) {
    if (!!queryStr) {
        queryStr = '?' + queryStr;
    } else {
        queryStr = '';
    }
    return this.http.get<any>(this.baseUrl + 'category-listing/' + queryStr);
}
 // End of above cod
// Method: declaring api to fetch items for dropdown in categpry-add in category list
getCategoryDropdownData() {
    return this.http.get<any>(this.baseUrl + 'category-create/');
}
 // End of above code
 // Method: declaring api to send data to add items to category-listing
saveCategoryData(data: any) {
    return this.http.post<any>(this.baseUrl + 'category-create/', data);
}
 // End of above code
 // Method: declaring api to edit data to add items to category-listing
editCategoryData(data: any) {
  return this.http.post<any>(this.baseUrl + 'category-edit/', data);
}
 // End of above code
}
 // End of code
